// autogenerated from /content/content.ru.md
    // via /src/utils/createContent.js
    export default {"info":{"title":"Арабский язык.\\n 1-я серия. От красоты к совершенству.","paramsArray":[["издатель: Аль-Джазира"]]},"content":[{"title":"1.1.","type":"richMedia","params":[],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","text":"В мире лошадей, верблюдов и пустынь было начало"},{"id":"002","text":"начало арабской речи."},{"id":"003","text":"Не было замкнутых пространств,"},{"id":"004","text":"зато продолжалось движение, смешивалось влияние и происходили переселения,"},{"id":"005","text":"затрудняющие изучение оттиска основы, оригинала,"},{"id":"006","text":"(с которого) множатся ответвления, а источник один."},{"id":"007","text":"Бесспорно, что рождение арабского языка из семитской семьи"},{"id":"008","text":"относится к сотням столетий до восхода солнца ислама."},{"id":"009","text":"(музыка)"},{"id":"010","text":"Учёные разошлись в определении родины первых семитов"},{"id":"011","text":"и не пришли к бесспорному мнению."},{"id":"012","text":"Вероятнее, она была в юго-западной части Аравийского полуострова,"},{"id":"013","text":"особенно в районе Хиджаза, Нежда и Йемена."},{"id":"014","text":"Мы говорим: арабский язык это часть"},{"id":"015","text":"семьи семитских языков, которая включает более 25 языков,"},{"id":"016","text":"и протянулась географически с Африканского рога:"},{"id":"017","text":"амхарский,"},{"id":"018","text":"геэз,"},{"id":"019","text":"и языки"},{"id":"020","text":"древне-эфиопские"},{"id":"021","text":"пока не достигает далёкого севера Ирака,"},{"id":"022","text":"это набатейский, арамейский, сирийский."},{"id":"023","text":"И арабские языки, на любой вкус и цвет,"},{"id":"024","text":"восходящие к одному из двух языков-источников."},{"id":"025","text":"Языку племён Аднан на севере арабского полуострова"},{"id":"026","text":"и языку племён Кахтан на юге."},{"id":"027","text":"За семь веков до ислама арабы были племенами,"},{"id":"028","text":"различными народами и племенами,"},{"id":"029","text":"каждая область говорила на диалекте ."},{"id":"030","text":"Эти диалекты, например, у жителей юга, потомков Кахтана в Йемене и вокруг него,"},{"id":"031","text":"жителей севера, потомков Аднана."},{"id":"032","text":"Все говорили на своём диалекте с соседями: племена Азд, Тайиб, Химьяр."},{"id":"033","text":"Как сказал Абу Амр Аль Аля,"},{"id":"034","text":"когда хотел рассказать о различии между севером и югом в языке,"},{"id":"035","text":"он сказал: Язык Химьяр не такой как наш язык,"},{"id":"036","text":"их арабский не как наш арабский."},{"id":"037","text":"У древнего арабского языка было"},{"id":"038","text":"много функциональных уровней:"},{"id":"039","text":"общий язык,"},{"id":"040","text":"язык поэзии, публичных речей, диалогов и хвалебных речей,"},{"id":"041","text":"и язык племён,"},{"id":"042","text":"который отклонялся от нормы по мере удаления от общего языка."},{"id":"043","text":"Было нормальным, что северный язык доминировал над южным,"},{"id":"044","text":"а язык (племени) Курейш над остальными языками"},{"id":"045","text":"из-за его религиозной, экономической и общественной значимости."},{"id":"046","text":"В пользу Курейша"},{"id":"047","text":"было его главенство в хадже."},{"id":"048","text":"А суть хаджа величайшее религиозное мероприятие,"},{"id":"049","text":"праздник торговли,"},{"id":"050","text":"и собрание,"},{"id":"051","text":"на котором собирались арабы и обменивались мнениями,"},{"id":"052","text":"старались сблизиться друг с другом на языке взаимопонимания,"},{"id":"053","text":"на объединяющей основе языка народа, где они находились."},{"id":"054","text":"Несмотря на то, что у них различались некоторые слова,"},{"id":"055","text":"большинство арабов знали"},{"id":"056","text":"этот диалект, потому что они контактировали"},{"id":"057","text":"ежегодно с"},{"id":"058","text":"этим племенем, даже до ислама."},{"id":"059","text":"Поэтому арабы на ярмарке хаджа"},{"id":"060","text":"и соседних ярмарках, сближались друг с другом на базе"},{"id":"061","text":"речи Курейш, на основе диалекта Курейш, на основе"},{"id":"062","text":"стандартов Курейш в повествовании (синтаксис) и звуковом исполнении (фонетика)."},{"id":"063","text":"Арабы устраивали ярмарки для торговли и обмена,"},{"id":"064","text":"а их социальная природа призывала"},{"id":"065","text":"к сближению в речи,"},{"id":"066","text":"переговорах о мнениях,"},{"id":"067","text":"оригинальности в стихах"},{"id":"068","text":"и соревнованию в красноречии"},{"id":"069","text":"Ярмарка Указа была"},{"id":"070","text":"красноречивым подтверждением почитания арабами красоты языка."},{"id":"071","text":"Доисламский период считается"},{"id":"072","text":"важным звеном в истории литературного арабского,"},{"id":"073","text":"который достиг непревзойденного уровня развития."},{"id":"074","text":"Арабский язык"},{"id":"075","text":"в доисламский период"},{"id":"076","text":"над ним преобладал характер"},{"id":"077","text":"языка, возникшего в среде пустыни."},{"id":"078","text":"Отсюда простота и материальность."},{"id":"079","text":"Поэтому у арабов преобладали"},{"id":"080","text":"имена естественной окружающей среды, окружавшей их, они говорили о разновидностях"},{"id":"081","text":"гор, дюн, песка."},{"id":"082","text":"Так язык стал материальным, скрывая слова, вроде: послание, судный день,"},{"id":"083","text":"сокровенное и весь абстрактный мир."},{"id":"084","text":"Богослужение было материальное,"},{"id":"085","text":"убеждения идолопоклонские, мышление,"},{"id":"086","text":"философия не развивались, так как требовался язык нового уровня."},{"id":"087","text":"Однако мир чувств, напротив, был богат."},{"id":"088","text":"Бедуин (кочевник) это человек первобытный, спонтанный, сильный от природы."},{"id":"089","text":"Поймало моё сердце печаль. Девушка"},{"id":"090","text":"С длинными ресницами и чёрными глазами,"},{"id":"091","text":"молодая, фигуристая, стройная, соблазнительная,"},{"id":"092","text":"стыдливая, великолепная, прекрасная как луна."},{"id":"093","text":"Наследие, которое принесла эпоха невежества для нас и истории"},{"id":"094","text":"до нас огромное."},{"id":"095","text":"В нём стихи, пословицы, притчи, речи и послания."},{"id":"096","text":"Эпоха невежества является одной из стадий языкового развития доарабского языка,"},{"id":"097","text":"того языка, который унаследовал эту стадию и стал языком зрелым, красивым, ёмким, волнующим."},{"id":"098","text":"Во-вторых, есть плавность в выражениях, простота, сила, ясность, эстетика."},{"id":"099","text":"Есть и кое-что удивительное в этом периоде поэзии:"},{"id":"100","text":"несмотря на то, что язык запутанный, необычный,"},{"id":"101","text":"язык следует законам ритма,"},{"id":"102","text":"просодии, музыки и размера,"},{"id":"103","text":"красоты образов, единства сюжета и множества целей."}]},"id":"001"},{"title":"1.2.","type":"richMedia","params":[],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","text":"Пришел Амр Аль-Кайс!"},{"id":"002","text":"Пришел Амр Аль-Кайс!"},{"id":"003","text":"Самым возвышенным,"},{"id":"004","text":"что создали древние посредством языка"},{"id":"005","text":"была поэзия."},{"id":"006","text":"А самым возвышенным в поэзии"},{"id":"007","text":"была (поэма) Муаллакат,"},{"id":"008","text":"прикрепленная к покрывалу Каабы"},{"id":"009","text":"из уважения к ней и признания"},{"id":"010","text":"за её эстетику."},{"id":"011","text":"Я крепость, построенная для народа Абс,"},{"id":"012","text":"когда строят герои крепость."},{"id":"013","text":"Я сделан из сердца скалы."},{"id":"014","text":"Когда рассыпятся горы,"},{"id":"015","text":"я не рассыплюсь."},{"id":"016","text":"Поэты (эпохи) невежества, вроде Амра Аль-Кайса, Антара (ибн Шаддада) и другие"},{"id":"017","text":"Набига Аз-Зубйани"},{"id":"018","text":"читали свои стихи, но не знали размера,"},{"id":"019","text":"стоп, стихотворного размера, о которых говорил Халил."},{"id":"020","text":"Поэты читали свои стихи"},{"id":"021","text":"и улучшали их, выбирая чистый язык,"},{"id":"022","text":"возвышенный язык, который понимали все арабы"},{"id":"023","text":"и не выбирали слова чужие, заимствованные."},{"id":"024","text":"Подтверждение (этому), что поэты создавали так называемые ежегодники."},{"id":"025","text":"Что такое ежегодники ? Это единая касыда, в которой располагался весь (творческий) год. Такое изобретение"},{"id":"026","text":"они писали её четыре месяца,"},{"id":"027","text":"потом редактировали и исправляли четыре месяца,"},{"id":"028","text":"потом демонстрировали критикам четыре месяца."},{"id":"029","text":"И если критики одобряли, то представляли людям."},{"id":"030","text":"Ты видел навоз (белых) газелей во дворах и на равнинах,"},{"id":"031","text":"словно это семена перца,"},{"id":"032","text":"как будто я накануне разлуки в ответственный день"},{"id":"033","text":"???"},{"id":"034","text":"???"},{"id":"035","text":"???"},{"id":"036","text":"???"},{"id":"037","text":"???"},{"id":"038","text":"???"},{"id":"039","text":"???"},{"id":"040","text":"???"},{"id":"041","text":"???"},{"id":"042","text":"???"},{"id":"043","text":"Были критики, оценивающие эту поэзию"},{"id":"044","text":"как замечательную, достойную и т.д."},{"id":"045","text":"Начало арабской критики было на этих ярмарках в"},{"id":"046","text":"периодическом оценивании поэтов и поэтесс."},{"id":"047","text":"Пусть даже люди соревновались,"},{"id":"048","text":"к ним прислушивался какой-нибудь критик, чтоб они поддерживали язык"},{"id":"049","text":"мутакариб (стиль стиха). Язык поэтов был"},{"id":"050","text":"языком высоким, возвышенным"},{"id":"051","text":"и во многом"},{"id":"052","text":"искусством с точки зрения техники, и"},{"id":"053","text":"красноречия и подобен общему языку"},{"id":"054","text":"для арабов севера Аравийского полуострова."},{"id":"055","text":"К скале придут вожди,"},{"id":"056","text":"словно она знамя,"},{"id":"057","text":"на вершине которой огонь."},{"id":"058","text":"Если бы Абу Басыр не читал мне стихи до тебя"},{"id":"059","text":"я бы сказал, что ты самый поэтический человек."},{"id":"060","text":"Я, клянусь Аллахом, лучший поэт, чем ты и она."},{"id":"061","text":"Пока ты говоришь что?"},{"id":"062","text":"Наши благородные веки блестят по утру,"},{"id":"063","text":"а мечи истекают кровью храбрецов"},{"id":"064","text":"???"},{"id":"065","text":"???"},{"id":"066","text":"???"},{"id":"067","text":"???"},{"id":"068","text":"???"},{"id":"069","text":"???"},{"id":"070","text":"???"},{"id":"071","text":"???"},{"id":"072","text":"Несомненно,"},{"id":"073","text":"у арабов (эпохи) невежества"},{"id":"074","text":"было знание о письменности"},{"id":"075","text":"особенно в южных поселениях,"},{"id":"076","text":"где найдены камни, скалы и другие предметы,"},{"id":"077","text":"использованные для записи."},{"id":"078","text":"Тогда арабская письменность была"},{"id":"079","text":"без точек,"},{"id":"080","text":"без огласовок."},{"id":"081","text":"Его основой было набатейское письмо,"},{"id":"082","text":"от которого оно произошло."},{"id":"083","text":"Арабская письменность до ислама"},{"id":"084","text":"ограничивалась (написанием) договоров и документов"},{"id":"085","text":"До"},{"id":"086","text":"ниспослания благородного Корана нашему господину, Мухаммаду, да благословит его Аллах и приветствует, арабская вязь"},{"id":"087","text":"не называлась вязью,"},{"id":"088","text":"она была письменностью"},{"id":"089","text":"и была записками"},{"id":"090","text":"тяжёлыми для записи."},{"id":"091","text":"Арабы восхищались и почитали умеющих писать, даже если сами не умели"},{"id":"092","text":"и арабы называли письменность клеймом, потому что они видели, что пишущий высекает, подобно клейму на одежде"},{"id":"093","text":"или клеймления денег."},{"id":"094","text":"Всё это признаки пережитков устного мышления,"},{"id":"095","text":"устной культуры, устного склада, устного языка."}]},"id":"002"},{"title":"1.3.","type":"richMedia","params":[],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","text":"Арабский язык оставался в таком положении"},{"id":"002","text":"до заката эпохи невежества,"},{"id":"003","text":"когда язычество и невежество сменилось эпохой ислама,"},{"id":"004","text":"с закреплением арабского"},{"id":"005","text":"как эмблемы для него и его последователей."},{"id":"006","text":"Первое божественное указание, исходящее от Всевышнего Аллаха это Читай"},{"id":"007","text":"мы рассматриваем это как первый закон, предписывающий нам читать на арабском языке"},{"id":"008","text":"11 аятов,"},{"id":"009","text":"все пришли как текст и предписание,"},{"id":"010","text":"потому что Коран"},{"id":"011","text":"снизошел, ниспослан, установлен и внушён"},{"id":"012","text":"на ясном арабском языке."},{"id":"013","text":"Здесь божественный замысел, в этом выборе,"},{"id":"014","text":"скрывающийся в его правильности,"},{"id":"015","text":"в его совершенстве, что это вселенский язык."},{"id":"016","text":"Я прибегаю к защите Аллаха от шайтана побиваемого."},{"id":"017","text":"С именем Аллаха, милостивого, милосердного"},{"id":"018","text":"Алиф, Лам, Ра (названия букв)"},{"id":"019","text":"Это — аяты ясного Писания."},{"id":"020","text":"Воистину, Мы ниспослали его в виде Корана на арабском языке, чтобы вы могли понять его."},{"id":"021","text":"Появление ислама и ниспослание"},{"id":"022","text":"его мудрого писания на ясном арабском языке"},{"id":"023","text":"создало широкий качественный переход"},{"id":"024","text":"в развитии арабского языка"},{"id":"025","text":"и изменило положение арабского языка,"},{"id":"026","text":"превратив его из локального в мировой."},{"id":"027","text":"Он двигался и двигался"},{"id":"028","text":"от границ к широкому распространению."},{"id":"029","text":"Коран, понятное дело,"},{"id":"030","text":"не только руководство для арабов:"},{"id":"031","text":"О, люди он обращается к человечеству,"},{"id":"032","text":"он рассказывает о существах и созданиях,"},{"id":"033","text":"говорит о разных небесных телах"},{"id":"034","text":"это означает, что Всевышний Аллах выделяет этот язык, обращаясь на нём ко всем своим созданиям."},{"id":"035","text":"Коран послужил арабскому укреплением,"},{"id":"036","text":"послужил арабскому записью, послужил арабскому распространением,"},{"id":"037","text":"и послужил арабскому укреплением"},{"id":"038","text":"его уровня в подаче, возвысил художественно и эстетически,"},{"id":"039","text":"достоинством и подачей."},{"id":"040","text":"А ещё глубже и сильнее, Коран послужил"},{"id":"041","text":"арабскому языку"},{"id":"042","text":"в сфере"},{"id":"043","text":"передачи"},{"id":"044","text":"знания"},{"id":"045","text":"и истин,"},{"id":"046","text":"связанных с рассказами о пророках, истории земли, истории вселенной,"},{"id":"047","text":"с начала времен,"},{"id":"048","text":"о нашем господине Адаме, и о настоящих убеждениях, нравах и ценностях."},{"id":"049","text":"Вот Йусуф (Иосиф) сказал своему отцу: «О мой отец! Я видел одиннадцать звезд, солнце и луну."},{"id":"050","text":"Я видел, как они поклонились мне»."},{"id":"051","text":"Было необходимо, чтобы для этого было влияние"},{"id":"052","text":"на устройство языка, то есть на структуру"},{"id":"053","text":"языка в общем"},{"id":"054","text":"в выражении тем, о которых рассказывают языки"},{"id":"055","text":"каждого народа."},{"id":"056","text":"Коран выстроил глубокую терминологию,"},{"id":"057","text":"ставшую полезной для всего арабского языка."},{"id":"058","text":"Никто сегодня не сможет сказать, что кораническая терминология"},{"id":"059","text":"монополизирована Кораном, и не входит в арабский."},{"id":"060","text":"Коран создал общеупотребительный словарный корпус."},{"id":"061","text":"Это как минимум 4 тысячи глубоких коранических терминов,"},{"id":"062","text":"несущих смыслы (связанные с) убеждениями, духовностью, нравственностью."},{"id":"063","text":"Язык хотим мы или нет это сосуд для мышления"},{"id":"064","text":"в разных его проявлениях:"},{"id":"065","text":"понятия, взгляды, знания, ощущения и чувства."},{"id":"066","text":"Коран оставил своим удивительным языком и стилем,"},{"id":"067","text":"явный след на мышлении, нраве и религии,"},{"id":"068","text":"рассеял мрак невежества."},{"id":"069","text":"И символом мусульман стало:"},{"id":"070","text":"Нун, клянусь письменной тростью и тем, что пишут."},{"id":"071","text":"Письменная трость закрепилась в исламской культуре как инструмент для записи"},{"id":"072","text":"и средство для заучивания Корана и его сохранения."},{"id":"073","text":"???"},{"id":"074","text":"Когда начал ниспосылаться Коран,"},{"id":"075","text":"окружили посланника Аллаха 40 свободных писцов,"},{"id":"076","text":"если ниспосылалось откровение, они спешили записывать."},{"id":"077","text":"И было у посланника Аллаха 60 писарей для откровения,"},{"id":"078","text":"других, не свободных (занятых)."},{"id":"079","text":"Если ниспосылалась сура или некоторые хадисы, им вменялось внимательно"},{"id":"080","text":"приписать это в таком-то месте, к такой-то суре."},{"id":"081","text":"Для книги Всевышнего Аллаха совмещалось запоминание наизусть и письменная запись"},{"id":"082","text":"Закончил ли ты считать аяты суры Пещера?"},{"id":"083","text":"Да."},{"id":"084","text":"Сколько их? 110."},{"id":"085","text":"Маленькое общество они не записывали десятков, но с Кораном стала осуществимой сотня."},{"id":"086","text":"При записи откровения произошел переход от общества устного к письменному,"},{"id":"087","text":"от невежественного общества к обществу письма и Корана."},{"id":"088","text":"Коран вывел арабский язык на письменный уровень."},{"id":"089","text":"Когда мы говорим письменный, имеем ввиду накапливаемость,"},{"id":"090","text":"развитие, запоминание, потому что то, что не записывается теряется,"},{"id":"091","text":"когда люди начали записывать, произошло накопление"},{"id":"092","text":"и цивилизация стала равняться на наследие,"},{"id":"093","text":"потому что, когда ты записываешь, ты сохраняешь."},{"id":"094","text":"Арабский не мог сохраняться, кроме как письменной фиксацией, а письменная фиксация стала необходима благодаря Корану."},{"id":"095","text":"Было необходимо"},{"id":"096","text":"существование шрифта и искусства, соответствующих самой великой книге, ниспосланной посланнику Аллаха, мир ему и благословение,"},{"id":"097","text":"поэтому соревновались каллиграфы"},{"id":"098","text":"в улучшении этого искусства"},{"id":"099","text":"и его обогащении,"},{"id":"100","text":"украшении, которое бы соответствовало ей и записи этого свитка."}]},"id":"003"},{"title":"1.4.","type":"richMedia","params":[],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","text":"Я закончил от последней 1/8 до первой 1/4 суры Аль-Бакара."},{"id":"002","text":"Начинай со второй четверти"},{"id":"003","text":"и не останавливайся, пока не закончишь первый хизб от всей суры."},{"id":"004","text":"Наш язык, арабский,"},{"id":"005","text":"в начале"},{"id":"006","text":"не имел точек (для согласных) и огласовок."},{"id":"007","text":"Точки согласных, вы знаете, насколько они важны"},{"id":"008","text":"для того, чтобы различать буквы."},{"id":"009","text":"Что касается огласовок,"},{"id":"010","text":"они появились позже"},{"id":"011","text":"и возможно это внесло вклад"},{"id":"012","text":"и привело к распространению речи,"},{"id":"013","text":"когда мусульмане начали распространять"},{"id":"014","text":"религию Всевышнего Аллаха"},{"id":"015","text":"в областях полуострова, в Персии, Леванте"},{"id":"016","text":"и других местах и стали смешиваться с"},{"id":"017","text":"мусульманами новые (люди) с другими языками."},{"id":"018","text":"В частности, так как Коран не имел"},{"id":"019","text":"точек и огласовок и начали"},{"id":"020","text":"появляться ошибки у новых мусульман. Так что Хузейфа"},{"id":"021","text":"Ибн Аль-Йаман, да будет доволен им Аллах, пришел"},{"id":"022","text":"за помощью к повелителю правоверных Усману и сказал: спаси исламскую умму, прежде"},{"id":"023","text":"чем она разделится в вопросе книги Аллаха."},{"id":"024","text":"???"},{"id":"025","text":"А вот рассказ о кочевнике."},{"id":"026","text":"Он принял ислам"},{"id":"027","text":"и устремился к учёбе,"},{"id":"028","text":"к заучиванию Корана,"},{"id":"029","text":"и отправился в Медину."},{"id":"030","text":"И в рассказе приводится,"},{"id":"031","text":"что ему читал"},{"id":"032","text":"один человек"},{"id":"033","text":"слова Всевышнего:"},{"id":"034","text":"Предупреждение от Аллаха и Его посланника к людям в день великого паломничества:"},{"id":"035","text":"Аллах отрекается от многобожников и Его посланника."},{"id":"036","text":"???"},{"id":"037","text":"???"},{"id":"038","text":"Если Аллах отрекается от своего посланника,"},{"id":"039","text":"то я отрекаюсь от тебя."},{"id":"040","text":"Нарушение правил,"},{"id":"041","text":"этот сбой изменил смысл"},{"id":"042","text":"и сделал кочевника или почти сделал отступником"},{"id":"043","text":"от ислама. Он понял (не тот) смысл и чуть не отступился от ислама."},{"id":"044","text":"Халиф Умар Ибн Аль-Хаттаб,"},{"id":"045","text":"когда дошла до его ушей эта новость,"},{"id":"046","text":"приказал"},{"id":"047","text":"читать Коран людям,"},{"id":"048","text":"только знатокам арабского языка."},{"id":"049","text":"Когда мусульмане заметили серьёзную нехватку, начали думать о"},{"id":"050","text":"защите арабского от ошибок, Корана от искажений, свитков от описок."},{"id":"051","text":"И всегда вдохновением был Коран, с которым был основательно связан арабский язык."},{"id":"052","text":"Первым обратившим внимание был Абу Асвад Ад-Дувалий,"},{"id":"053","text":"сподвижник нашего господина Али,"},{"id":"054","text":"когда его дочь говорила с ошибками перед ним."},{"id":"055","text":"Он не был занят сражениями и завоеваниями, а она воспитывалась мамой неарабкой"},{"id":"056","text":"и научилась от неё ошибкам в языке. Он пришел с ней"},{"id":"057","text":"на собрание Али Ибн Абу Талиба"},{"id":"058","text":"и сказал: Пропал арабский язык и пропал Коран, о повелитель правоверных."},{"id":"059","text":"И ответил ему наш господин Али: Поистине речь это имя, действие и частица,"},{"id":"060","text":"так следуй этим направлением, о Абу Асвад!"},{"id":"061","text":"То есть он начал составление этих правил и"},{"id":"062","text":"название грамматика произошло от слова нашего господина Али"},{"id":"063","text":"следуй этим направлением, о Абу Асвад"},{"id":"064","text":"и продиктовал своему писцу:"},{"id":"065","text":"Если ты видишь как я вытягиваю (адумму) губы при (произнесении) буквы,"},{"id":"066","text":"ставь точку сбоку от неё,"},{"id":"067","text":"а если ты видишь, что я открываю (афтаху) губы при букве,"},{"id":"068","text":"ставь точку над ней,"},{"id":"069","text":"а если видишь, что я сгибаю (аксиру) губы при букве,"},{"id":"070","text":"ставь точку снизу,"},{"id":"071","text":"а если я делаю вот так,"},{"id":"072","text":"через нос, ставь две точки."},{"id":"073","text":"С именем Аллаха, милостивого, милосердного"},{"id":"074","text":"когда пришел."},{"id":"075","text":"Эта система точек, которая развилась далее"},{"id":"076","text":"в то, что известно под названием движения флексии"},{"id":"077","text":"или огласовки."},{"id":"078","text":"Историки единогласны, что Абу Асвад Ад-Дувалий"},{"id":"079","text":"сделал важный поворот в истории арабского языка"},{"id":"080","text":"и ознаменовал новый век взаимоотношений с его нормами и правилами."},{"id":"081","text":"Омеййады много трудились, чтобы распространять вместе с исламом и арабский язык."},{"id":"082","text":"Разумеется, с ограниченными средствами. Не было в те времена школ,"},{"id":"083","text":"мечети были школами."},{"id":"084","text":"Омеййады заботились об арабском языке, потому что они были арабами,"},{"id":"085","text":"их вкус арабский, склад ума арабский, религия арабская, темперамент арабский."},{"id":"086","text":"И их забота об арабском была от их арабской натуры, как сказал Джахиз:"},{"id":"087","text":"от их религиозности и заботы о Коране."},{"id":"088","text":"Как мы уже сказали, в их время, при содействии их правителей"},{"id":"089","text":"завершили огласовывание текста."},{"id":"090","text":"Их заслуга также введение шрифта с диакритическими точками."},{"id":"091","text":"Их другая заслуга, очень важная, что они ввели арабский язык в гос.учреждениях."},{"id":"092","text":"В 87 году хиджры Халиф издал смелый указ об арабизации гос.структур."},{"id":"093","text":"Арабизация гос.учреждений потребовала и арабизацию монет."},{"id":"094","text":"Появился первый арабский динар с надписями на арабском: некоторых аятов,"},{"id":"095","text":"имен и знаков."}]},"id":"004"},{"title":"1.5.","type":"richMedia","params":[],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","text":"Если бы развалины могли говорить,"},{"id":"002","text":"то поведали бы о загадке распространения арабского языка в Андалусии"},{"id":"003","text":"как местные жители встретили его,"},{"id":"004","text":"что церковнослужители были вынуждены переводить молитвы на арабский,"},{"id":"005","text":"чтобы христиане понимали их."},{"id":"006","text":"Арабский язык распространился по Андалусии в течении 50 лет, половины века."},{"id":"007","text":"Это феноменальный срок в мировых масштабах. Как он распространился?"},{"id":"008","text":"По причине политического указа, изданного халифом Абд Ар-Рахманом Аль-Авсатом,"},{"id":"009","text":"велевшим людям говорить на арабском языке."},{"id":"010","text":"Дело в том, что переживали коренные жители"},{"id":"011","text":"(вандалы) притеснение и насилие со стороны колонизировавших их коптов"},{"id":"012","text":"и из-за этого они принимали ислам и арабский язык добровольно и увлечённо."},{"id":"013","text":"И в добавок к этому достоинство арабов, которых отправили Омеййады на завоевания, до их падения (их династии)."},{"id":"014","text":"Они отправляли арабские племена из группы Кайс и Кахтан (йеменцев),"},{"id":"015","text":"среди которых были поэты, проповедники и писатели,"},{"id":"016","text":"и все они настоящие арабы."},{"id":"017","text":"Они образовали учёный класс в Андалусии."},{"id":"018","text":"Этот класс (слой) был большим по количеству"},{"id":"019","text":"и сильным по качеству."},{"id":"020","text":"Европейцы, которые хотели специализироваться в медицине или инженерии начали"},{"id":"021","text":"отправлять своих студентов в Андалусию и в столицу Кордову."},{"id":"022","text":"И первым, что им приходилось выучить, был арабский язык."},{"id":"023","text":"Кто читал рассказы о монахах, священниках и востоковедах,"},{"id":"024","text":"(заметил) как они мастерски владели арабским языком"},{"id":"025","text":"и владели Кораном и хадисами лучше, чем некоторые арабы."},{"id":"026","text":"Христианским институтам вменялось в обязанность"},{"id":"027","text":"обучать своих учеников арабскому языку и в совершенстве владеть им."},{"id":"028","text":"???"},{"id":"029","text":"???"},{"id":"030","text":"???"},{"id":"031","text":"Мы подверглись воздействию других народов, как и они нашему,"},{"id":"032","text":"мы брали от них также, как они брали от нас."},{"id":"033","text":"Забота об арабском и языковое сочинительство"},{"id":"034","text":"появились в век Аббасидов,"},{"id":"035","text":"потому что в этом веке начали"},{"id":"036","text":"появляться языковые проблемы, так скажем,"},{"id":"037","text":"по причине многочисленности неарабов."},{"id":"038","text":"Потому что гос.учреждения стали управляться неарабами без присмотра за ними"},{"id":"039","text":"на уровне языковой практики и оказались в положении,"},{"id":"040","text":"когда нужно было принять меры по исправлению этого."},{"id":"041","text":"Перед нами арабские аббасидские поэты, великие поэты,"},{"id":"042","text":"мы видим у них говор или что называется диалектными словами,"},{"id":"043","text":"или простонародными и т.п."},{"id":"044","text":"В поэзии эпохи Аббасидов встречаются заимствованные, неарабские слова у великих поэтов"},{"id":"045","text":"В то же время в аббасидской поэзии встречаются слова"},{"id":"046","text":"из древнего вычурного арабского языка, неестественные и непонятные"},{"id":"047","text":"даже арабам."},{"id":"048","text":"С этого вызова началась борьба учёных."},{"id":"049","text":"И забота аббасидского государства о них (ученых) не могла игнорировать это."},{"id":"050","text":"Академическая борьба за сохранение арабского языка. Они начали систематизацию."},{"id":"051","text":"И систематизация переносит вас от спонтанного понимания языка к сознательному пониманию"},{"id":"052","text":"от стихийного понимания языка"},{"id":"053","text":"к практике направленного изучения языка, подобного изучению математики, через правила."},{"id":"054","text":"Учёные были вынуждены"},{"id":"055","text":"придумать правила, регулирующие этот язык."},{"id":"056","text":"И если кто-то выходит за рамки этих правил, тот выходит (из норм) языка."},{"id":"057","text":"Для этого установили правила так,"},{"id":"058","text":"как не устанавливали у арабов до ислама."},{"id":"059","text":"Установление всех формальностей произошло в эпоху Аббасидов."},{"id":"060","text":"Установление всех правил произошло в эпоху Аббасидов."},{"id":"061","text":"Великие языковеды, как Халил Ибн Ахмад Аль-Фарахиди, Сибавайх,"},{"id":"062","text":"его ученик Абу Али Ар-Румани и последующие языковеды были"},{"id":"063","text":"на протяжении эпохи Аббасидов при дворах их правителей и королей."},{"id":"064","text":"Эти правила, над которыми постарались древние (ученые)"},{"id":"065","text":"в их недрах, или как мы говорили ранее, в основах,"},{"id":"066","text":"впереди чудесный благородный Коран."},{"id":"067","text":"Второй источник"},{"id":"068","text":"хадисы господина посланников, избранника, да благословит его Аллах и приветствует."},{"id":"069","text":"Третий источник поэзия."},{"id":"070","text":"Поэзия, как вы знаете"},{"id":"071","text":"это стихи кочевых арабов."},{"id":"072","text":"Четвертый источник арабы:"},{"id":"073","text":"настоящие, благородные,"},{"id":"074","text":"которые говорили на языке с детства,"},{"id":"075","text":"от природы и интуитивно разбирались в этом."},{"id":"076","text":"Во-первых, была строгая карта"},{"id":"077","text":"племен, до которых не могли добраться."},{"id":"078","text":"Потому что племена окраин, всё же соприкасались и переплетались,"},{"id":"079","text":"хотя и не селились в городах."},{"id":"080","text":"Они (учёные) взяли сердце арабского полуострова,"},{"id":"081","text":"в закрытой пустыне и сохранили то, что было у них."},{"id":"082","text":"Было установлено это географическое условие сердце закрытой арабской пустыни, и установлено временное условие"},{"id":"083","text":"заимствовалось из литературного языка до конца 2-го века в городах"},{"id":"084","text":"и до конца 4-го века у бедуинов,"},{"id":"085","text":"потому что после этого (язык) был ненадежным."}]},"id":"005"},{"title":"1.6.","type":"richMedia","params":[],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","text":"Когда появились могущественные империи, вроде Аббасидской,"},{"id":"002","text":"они соревновались в привлечении каллиграфов,"},{"id":"003","text":"устраивали соревнования по написанию Священного Писания и государственных документов и т.п."},{"id":"004","text":"Благодаря этому созрели многие каллиграфы. Упомяну каллиграфа Ибн Мукла"},{"id":"005","text":"и каллиграфа Якута Мустасима. Имя каллиграфа отсылало"},{"id":"006","text":"к периоду правления султана или правителя."},{"id":"007","text":"В эту эпоху"},{"id":"008","text":"объединились культурные, религиозные и политические факторы"},{"id":"009","text":"для реализации важного прорыва в исламском мире."},{"id":"010","text":"Возник ранний интерес к мирским наукам"},{"id":"011","text":"и образованию новых знаний."},{"id":"012","text":"Исламский мир был в расцвете своей цивилизации"},{"id":"013","text":"и пользовался тем, что цивилизации взаимодействовали между собой"},{"id":"014","text":"и получали от этого взаимодействия важное для себя."},{"id":"015","text":"Всё это прибавляло арабскому языку цену,"},{"id":"016","text":"а арабской умме энергию."},{"id":"017","text":"Он был языком первого мира, в то время как мы были первым миром."},{"id":"018","text":"В то время наш язык был языком политики, экономики, торговли, науки, знаний, астрономии,"},{"id":"019","text":"а также создания и распространения переводов."},{"id":"020","text":"Руками арабов, которые объединили цивилизации,"},{"id":"021","text":"то есть связали цивилизации между собой."},{"id":"022","text":"Движение переводов началось как погоня, как страсть,"},{"id":"023","text":"потому что исламская религия религия развития и она призывает людей созерцать, извлекать уроки и искать знания, пусть даже в Китае."},{"id":"024","text":"Эта страсть привела мусульман в то время к желанию переводить."},{"id":"025","text":"Когда пришла эпоха Аббасидов"},{"id":"026","text":"и логика вещей, которая была накоплена и достигнута в цивилизацию Аббасидов,"},{"id":"027","text":"достигнутая официальными решениями, высшими указами"},{"id":"028","text":"и общественным сознанием, привели к этим переводам."},{"id":"029","text":"Движение по переводу и переписыванию,"},{"id":"030","text":"Дом мудрости,"},{"id":"031","text":"у них была основная роль"},{"id":"032","text":"в объединении"},{"id":"033","text":"и развитии арабского мира"},{"id":"034","text":"и арабского языка для того, чтобы культуры"},{"id":"035","text":"и науки иностранцев были заимствованы"},{"id":"036","text":"Приобретения цивилизации и достижения,"},{"id":"037","text":"которые были в медицине, инженерии,"},{"id":"038","text":"астрономии и других науках"},{"id":"039","text":"переведены с греческого языка"},{"id":"040","text":"на арабский язык."},{"id":"041","text":"Поговорим о службе, которую сослужили переводы для уммы."},{"id":"042","text":"Привнесение знаний и наук как это отразилось на арабском?"},{"id":"043","text":"Арабский язык включил терминологию этих наук."},{"id":"044","text":"Некоторые были неправильными"},{"id":"045","text":"(с точки зрения) идеального арабского, войдя в него извне,"},{"id":"046","text":"но необходимыми, как слово философия"},{"id":"047","text":"или слово география,"},{"id":"048","text":"или слово астролябия. Мы не знали этих слов."},{"id":"049","text":"Преобладали термины из астрономии, математики, философии, географии,"},{"id":"050","text":"геологии и многих наук, которые арабы не знали."},{"id":"051","text":"Мы взяли эти слова так, как они звучали, и арабизировали их."},{"id":"052","text":"Они вошли в тело языка и стали частью языка."},{"id":"053","text":"Не проблема, что мы воспользовались другими культурами,"},{"id":"054","text":"взяли от них, перевели их"},{"id":"055","text":"на наш арабский язык точно"},{"id":"056","text":"не искажая смысла"},{"id":"057","text":"языка оригинала."},{"id":"058","text":"И это один из способов развития арабского языка."},{"id":"059","text":"Иначе перевод был бы плохим, недостоверным."},{"id":"060","text":"Тексты получались бы плохими"},{"id":"061","text":"Но эти тексты не соотносятся с основным языком, потому что они не касаются его правил."},{"id":"062","text":"и не становятся (враждебной) колонной в цельном языке."},{"id":"063","text":"Наоборот, мы развились для словаря"},{"id":"064","text":"богатого, с науками и знаниями,"},{"id":"065","text":"и он стал языком, на который делался перевод."},{"id":"066","text":"Это знание, и это приобретение становится хранителем"},{"id":"067","text":"и продолжает переваривание, производство и экспорт этого"},{"id":"068","text":"знания и придает ему (языку) дополнительную ценность."},{"id":"069","text":"Но у всего есть свой предел."},{"id":"070","text":"И после падения Гранады и Багдада"},{"id":"071","text":"с арабским оплотом случилась болезнь"},{"id":"072","text":"и потеряли арабы своё место в цивилизации"},{"id":"073","text":"и проникла слабость в язык,"},{"id":"074","text":"который был сосудом мысли и знаний,"},{"id":"075","text":"символом развития."}]},"id":"006"},{"title":"1.7.","type":"richMedia","params":[],"content":{"phrases":[{"id":"000","start":0,"end":0,"text":""},{"id":"001","text":"Потом пришла эпоха Османов"},{"id":"002","text":"и отношения с арабским языком стали запутанными"},{"id":"003","text":"и неясными."},{"id":"004","text":"С приходом ислама арабский язык пришел естественным путем в этот регион."},{"id":"005","text":"После этого, известно, что разные государства сменяли (друг друга)"},{"id":"006","text":"на этих территориях и пришли сельджуки"},{"id":"007","text":"и, как известно, победили в сражении"},{"id":"008","text":"при Манцикерте в 1070 по хиджре"},{"id":"009","text":"ой, то есть от Рождества (Христова)."},{"id":"010","text":"После этого сельджуки выбрали"},{"id":"011","text":"арабские буквы как инструмент и способ передачи"},{"id":"012","text":"их литературы, знаний, культуры,"},{"id":"013","text":"официальных посланий и т.д."},{"id":"014","text":"Турки, когда приняли ислам, оставили свой язык"},{"id":"015","text":"и перестали писать на уйгурском"},{"id":"016","text":"и на готтурк."},{"id":"017","text":"Они оставили их и начали писать арабскими буквами."},{"id":"018","text":"Это указывает на их любовь к арабскому языку."},{"id":"019","text":"Арабские буквы, на самом деле, использовались в Турции более 1000 лет назад."},{"id":"020","text":"Арабский язык был в Османской империи одним из важнейших."},{"id":"021","text":"Известно, что арабский был языком науки и знаний в Османской Империи, в то время как"},{"id":"022","text":"турецкий был языком государства и учреждений."},{"id":"023","text":"Они любили арабский и почитали как язык Корана."},{"id":"024","text":"Отсюда их страсть к арабским буквам и переход на них."},{"id":"025","text":"Величайшими каллиграфами были турки"},{"id":"026","text":"и лучшие письменные османские росписи (сделали) турки."},{"id":"027","text":"Османская империя в свои золотые века поддерживала это искусство"},{"id":"028","text":"и оно стало одним из компонентов, символизирующих это государство."},{"id":"029","text":"Каллиграфы стали пользоваться всесторонним покровительством"},{"id":"030","text":"и пользовались уважением."},{"id":"031","text":"Для этого были прочные основы в эпоху Османского халифата. Султан должен был знать каллиграфию"},{"id":"032","text":"и положение каллиграфа было подобно положению министра."},{"id":"033","text":"Один султан даже держал чернильницу"},{"id":"034","text":"для известного каллиграфа Хафиза Османа"},{"id":"035","text":"и говорил: Я не представляю, что появится каллиграф вроде тебя"},{"id":"036","text":"или что появлялся подобный тебе до тебя или (появится) после тебя."},{"id":"037","text":"И сказал ему Хафиз Осман: Если будут султаны вроде тебя, держащие"},{"id":"038","text":"чернильницу для своего мастера, появятся тысячи Хафизов Османов."},{"id":"039","text":"Османское государство"},{"id":"040","text":"получало сильный и положительный эффект от привлечения известных каллиграфов."},{"id":"041","text":"Их приглашали во дворец султана"},{"id":"042","text":"и дарили им подарки."},{"id":"043","text":"Это служило целям Османского государства,"},{"id":"044","text":"чтобы были независимые личности, чтобы были мнения, отличающиеся от существующих."},{"id":"045","text":"От основания Османского государства 1300 лет назад и позже"},{"id":"046","text":"мы видим, что учёные писали на арабском"},{"id":"047","text":"с первого дня на темы тафсира, хадисов, а также медицины"},{"id":"048","text":"и астрономии. Все ученые писали свои труды на арабском языке."},{"id":"049","text":"Вся научная традиция была построена"},{"id":"050","text":"на арабском языке."},{"id":"051","text":"Не зная хорошо арабский язык ты не мог обучиться ни одной науке."},{"id":"052","text":"Почти все слова османского языка, особенно научная терминология"},{"id":"053","text":"были из арабского языка, или производными от него."},{"id":"054","text":"Конечно, это было из-за богатства арабского языка."},{"id":"055","text":"Библиотеки и научные манускрипты"},{"id":"056","text":"все были на арабском языке."},{"id":"057","text":"Салим I, когда вернулся из путешествия в Каир"},{"id":"058","text":"и привез священные обязательства,"},{"id":"059","text":"превратил Халифат в Османское государство,"},{"id":"060","text":"собрался попросить исламского ученого,"},{"id":"061","text":"чтобы он сделал арабский язык официальным в Османском государстве,"},{"id":"062","text":"но к сожалению, шейх уль-ислам (не нашел)"},{"id":"063","text":"доводов и аргументов для этого."},{"id":"064","text":"Связь османов"},{"id":"065","text":"с арабским языком запутанная,"},{"id":"066","text":"не такая как связь с исламом. Связь с исламом ясная."},{"id":"067","text":"???"},{"id":"068","text":"Эта святость арабского языка в его тесной связи с Кораном"},{"id":"069","text":"и они полагали, что назначение языка в службе Корану."},{"id":"070","text":"Османское правление,"},{"id":"071","text":"которое длилось более четырёх веков,"},{"id":"072","text":"столкнулось с арабским языком"},{"id":"073","text":"как с большим вызовом программе систематического отуречивания,"},{"id":"074","text":"которую власти"},{"id":"075","text":"поддерживали или практиковали."},{"id":"076","text":"Нельзя говорить, что османы продолжали"},{"id":"077","text":"поддерживать арабский, в то время как они приняли указ об отуречивании."},{"id":"078","text":"И это отуречивание делалось во всём турецком мире, и арабы входили в него."},{"id":"079","text":"Они отуречивали не только язык, но и человеческие ресурсы."},{"id":"080","text":"Мухаммад Али Аль-Албаний, который предпринял возрождение Египта,"},{"id":"081","text":"человек, специализировавшийся на быстром возрождении."},{"id":"082","text":"Это египетское чудо связано с ним."},{"id":"083","text":"Был он албанец или османец (не важно), его привели османы."},{"id":"084","text":"Он правил Египтом"},{"id":"085","text":"40 лет не зная ни одной арабской буквы."},{"id":"086","text":"При этом хорошо да!"},{"id":"087","text":"С сердцем, полным веры да!"},{"id":"088","text":"С пренебрежением к ним да. Турки начали"},{"id":"089","text":"сокращать арабский язык до нескольких научных дисциплин."},{"id":"090","text":"Когда Наполеон вторгся в Египет,"},{"id":"091","text":"это прибавило трудностей арабскому языку:"},{"id":"092","text":"обострилась болезнь,"},{"id":"093","text":"пересохли перья"},{"id":"094","text":"и затерялся язык во мраке невежества."},{"id":"095","text":"Погасли умы,"},{"id":"096","text":"наступил закат."}]},"id":"007"}],"hiddenContent":[]}